.indicator-pill.grey:before {
  background: white;
}

.indicator-pill.grey:after {
  background: white;
}

.indicator-pill-right.grey {
  background: #24b0c7;
  color: white;
}

.indicator-pill-right.grey:before {
  background: white;
}

.indicator-pill-right.grey:after {
  background: white;
}

.indicator-pill-round.grey {
  background: #24b0c7;
  color: white;
}

.indicator-pill-round.grey:before {
  background: white;
