}

.layout-side-section {
  border-radius: var(--border-radius-md);
}

.like-disabled-input {
  background: #24b0c7;
  color: white;
}

.badge-dark {
  background-color: #4ec3d5;
  color: white;
}

.page-title {
  background-color: #24b0c7;
  color: white;
}

.page-title::before {
  border-bottom: 3px solid #fff;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
